@charset "utf-8";

/* 입력폼 서식 */
.form{
  display: flex;
  padding: 1rem;
}

.form input{
  flex: 0.98;
  font-size: 1rem;
  border-radius: 12px;
  padding: 0.7rem 1rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #fac5f1;
  margin: 0 1rem;
}

/* 추가(입력)버튼 서식 */
.create_btn{
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background: #e645b0;
  border-radius: 3px;
  color: #fff;
  font-weight: 600;
  border: none;
  cursor: pointer;
}
.create_btn:hover{
  background: #f780d3;
}
