@charset "utf-8";

.todo_item{
  padding: 1rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 40px;
  padding: 1rem 4.5rem 1rem 2.5rem;
}
.todo_item:hover{
  background-color: #ccc;
}
/* todo-item 에 마우스가 있을 때만 .remove 보이기 */
.todo_item:hover .remove{
  opacity: 1;
}

/* todo_item 사이에 윗 테두리 */
.todo_item + .todo_item {
  border-top: 1px solid #f1f3f5;
}

/* 삭제 버튼 서식 */
.remove{
  opacity: 0;
  font-weight: bold;
  color: rgb(38, 0, 255);
  cursor: pointer;
  margin-left: auto;      /* ← 핵심 */
   order: 2;
}


.todo_text{
  flex: 1;
  word-break: break-all;
  order: 1;
}
/* 해당 글 목록 클릭 시 글자 서식이 흐리고, 가운데 줄 표시 하도록 함. */
.checked{
  text-decoration: line-through; /* 취소선 서식 */
  color: #adb5bd; /* 글자 흐리게 */
}
.check_mark{
  font-size:1.5rem;
  line-height: normal;
  color: #f00;
  font-weight: bold;
}